


















































































import {Component, Vue} from 'vue-property-decorator';
import {GetAllProductsDocument, GetProductAggregateDocument} from '../../../../gql';
import ProductActions from '../../../../components/products/product-actions.vue';

@Component({
    layout: 'console',
    components: {
        ProductActions
    },
    apollo: {
        productAggregate: {
            query: GetProductAggregateDocument
        },
        products: {
            query: GetAllProductsDocument,
            variables() {
                return {
                    limit: this.limit,
                    offset: this.offset,
                    iLike: `%${this.search}%`
                }
            },
            pollInterval: 3000
        }
    }
})
export default class Index extends Vue {
    private allProducts = []

    private productAggregate

    // Paging
    private last: number = 0
    private first: number = 10
    private after: any = null
    private before: any = null
    private hasPrev: boolean = false;
    private hasNext: boolean = false;

    private limit = 50
    private offset = 0
    private search: string = ''

    private products

    // Table
    private gridOptions: any = {};
    private gridApi: any = null;
    private defaultColDef = {
        sortable: true,
        editable: false,
        resizable: true,
        suppressMenu: true
    };
    private columnDefs = [
        {
            headerName: 'Name',
            filter: false,
            field: 'productName'
        },
        {
            headerName: 'Slug',
            filter: false,
            field: 'slug'
        },
        {
            headerName: 'Actions',
            cellRendererFramework: 'ProductActions'
        }
    ]

    onGridReady() {
        this.gridApi = this.gridOptions!.api;
        this.gridApi!.sizeColumnsToFit();
    }

    onCreateProduct() {
        this.$router.push(`/app/catalog/products/createproduct`)
    }
}
